    /*timestamp日期轉換中文 */
    renderChatInfoDateTimeFormat(createDateTime) {

        let strTime = "";
        let dateTime = new Date(createDateTime);
        let create_year = dateTime.getFullYear();
        let monthSplit = (parseInt(dateTime.getMonth()) + 1).toString().split("");
        let create_month = monthSplit[1] ? parseInt(dateTime.getMonth()) + 1 : "0" + monthSplit[0];
        let dateSplit = dateTime
            .getDate()
            .toString()
            .split("");
        let create_date = dateSplit[1] ? dateTime.getDate() : "0" + dateSplit[0];
        let now = new Date();
        let nowYear = now.getFullYear();
        let nowMonthSplit = (parseInt(now.getMonth()) + 1).toString().split("");
        let nowMonth = nowMonthSplit[1] ? parseInt(now.getMonth()) + 1 : "0" + nowMonthSplit[0];
        let nowDateSplit = now
            .getDate()
            .toString()
            .split("");
        let nowDate = nowDateSplit[1] ? now.getDate() : "0" + nowDateSplit[0];
        let date_now = new Date(nowYear, nowMonth, nowDate);
        let date_create = new Date(create_year, create_month, create_date);
        let gap_date = (date_now - date_create) / 86400000;
        if (gap_date < 1) {

            strTime = format(dateTime, 'a hh:mm', { locale: langControl.local });

            //strTime = dateTime.toLocaleString(langControl.initType, optionsADay);
        } else if (gap_date === 1) {
            strTime = langControl.data[langControl.initType].YESTERDAY;
        } else if (gap_date > 1 && gap_date < 7) {
            strTime = dateTime.toLocaleDateString(langControl.initType, optionsUnderWeek);
        } else if (parseInt(nowYear) - parseInt(create_year) === 1) {
            strTime = dateTime.toLocaleDateString(langControl.initType, optionsYear);
        } else if (gap_date >= 7) {
            strTime = dateTime.toLocaleDateString(langControl.initType, optionsBeyondWeek);
        }
        return strTime;
    },

    isSameDay(d1, d2) {
        return d1.getFullYear() === d2.getFullYear() &&
            d1.getMonth() === d2.getMonth() &&
            d1.getDate() === d2.getDate();
    },

    isYesterday(d2) {
        let d1 = new Date(new Date().getTime() - millisecondADay);
        return d1.getFullYear() === d2.getFullYear() &&
            d1.getMonth() === d2.getMonth() &&
            d1.getDate() === d2.getDate();
    },
    isSameYear(d2) {
        let d1 = new Date();
        return d1.getFullYear() === d2.getFullYear();
    },
    renderChatContentDate(createDateTime) {

        let now = new Date();
        let createDate = new Date(createDateTime);

        if (this.isSameDay(now, createDate)) {
            return langKey.TODAY;
        }
        if (this.isYesterday(createDate)) {
            return langKey.YESTERDAY;
        }

        if (!this.isSameYear(createDate)) {
            return createDate.toLocaleDateString(langControl.initType, {
                year: "numeric",
                month: "2-digit",
                day: "2-digit",
                weekday: "short"
            });
        }

        return createDate.toLocaleDateString(langControl.initType, {
            month: "2-digit",
            day: "2-digit",
            weekday: "short"
        });

    },

