    imageBase64toBlob(base64ImageStr) {

        //data:image/png;base64,iVBORw0KGgoAAxxxx
        let temp = base64ImageStr;
        let s = temp.indexOf(":");
        let e = temp.indexOf(";");
        let b = temp.indexOf("base64,");
        let contentType = temp.substring(s + 1, e);
        let b64Data = temp.substring(b + 7);
        let sliceSize = 512;
        const byteCharacters = atob(b64Data);
        const byteArrays = [];
        for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            const slice = byteCharacters.slice(offset, offset + sliceSize);

            const byteNumbers = new Array(slice.length);
            for (let i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }

            const byteArray = new Uint8Array(byteNumbers);
            byteArrays.push(byteArray);
        }
        return new Blob(byteArrays, { type: contentType });
    },

    /**初始化頭像剪裁框與畫布*/
    initCropBox(inputFileId, cropper, cropCanvasSize, cropSize) {

        let w = document.getElementById(inputFileId).width;
        let h = document.getElementById(inputFileId).height;
        if (w < cropCanvasSize) {
            w = cropCanvasSize;
        }
        if (h < cropCanvasSize) {
            h = cropCanvasSize;
        }
        let leftTop = (cropCanvasSize - cropSize) / 2;

        // 圖置中
        let canvasLeft = 0;
        let canvasTop = 0;
        if (w > cropCanvasSize) {
            canvasLeft = -((w - cropCanvasSize) / 2);
        }
        if (h > cropCanvasSize) {
            canvasTop = -((h - cropCanvasSize) / 2);
        }

        // 設定畫布與裁剪框大小
        cropper.setCanvasData({
            width: w,
            height: h,
            left: canvasLeft,
            top: canvasTop
        })

        cropper.setCropBoxData({
            width: cropSize,
            height: cropSize,
            left: leftTop,
            top: leftTop
        });
    },

    cropBoxReset(cropper, cropCanvasSize, cropSize) {

        let { left, top, width, height } = cropper.getCanvasData();

        let left2, top2;
        if (left < 0) {
            left2 = width - (-left);
        } else {
            left2 = left + width;
        }

        if (top2 < 0) {
            top2 = height - (-top);
        } else {
            top2 = top + height;
        }

        // 計算4角xy
        let cropLeft1 = (cropCanvasSize - cropSize) / 2;
        let cropLeft2 = cropLeft1 + cropSize;

        let cropTop1 = (cropCanvasSize - cropSize) / 2;
        let cropTop2 = cropTop1 + cropSize;

        //console.debug("left:%d ,left2:%d, cropLeft1:%d, cropLeft2:%d", left, left2, cropLeft1, cropLeft2)

        // 圖左邊大於框左邊界
        if (left > cropLeft1) {
            cropper.setCanvasData({ left: cropLeft1 });
        }
        // 圖右小於框右邊界
        if (left2 < cropLeft2) {
            cropper.setCanvasData({ left: left + (cropLeft2 - left2) });
        }
        //console.debug("top:%d, top2:%d, cropTop1:%d, cropTop2:%d", top, top2, cropTop1, cropTop2)
        // 圖上邊大於框上邊界
        if (top > cropTop1) {
            cropper.setCanvasData({ top: cropTop1 });
        }
        // 圖下邊小於框下邊界
        if (top2 < cropTop2) {
            cropper.setCanvasData({ top: top + (cropTop2 - top2) });
        }

    },


