    fileTypeClassify(fileName) {

        let fileType;

        if (fileName) {
            let extension = fileName.slice(((fileName.lastIndexOf(".") - 1) >>> 0) + 2).toLowerCase(); // 副檔名
            //目前接受的副檔名
            let typeImage = ["jpg", "jpeg", "png", "svg", "gif", "bmp"];
            let typeVideo = ["mp4"];
            let typeAudio = ["mp3", "wav"];
            let typePdf = ["pdf"];
            let typeWord = ["docx"];
            let typeExcel = ["xlsx"];
            let typePpt = ["ppt", "pptx"];
            let typeZip = ["zip", "7z"];
            let typeTxt = ["txt"];


            if (typeImage.includes(extension)) {
                fileType = "img";
            } else if (typeVideo.includes(extension)) {
                fileType = "video";
            } else if (typeAudio.includes(extension)) {
                fileType = "audio";
            } else if (typePdf.includes(extension)) {
                fileType = "pdf";
            } else if (typeWord.includes(extension)) {
                fileType = "word";
            } else if (typeExcel.includes(extension)) {
                fileType = "excel";
            } else if (typeTxt.includes(extension)) {
                fileType = "txt";
            } else if (typePpt.includes(extension)) {
                fileType = "ppt";
            } else if (typeZip.includes(extension)) {
                fileType = "zip";
            } else {
                fileType = "file";
            }

            //for TEST
            //console.debug("#fileTypeClassify FileType:[%s], fileName:%s", fileType, fileName);
        }

        return fileType;
    },


    /***/
    timeDuration(time1, time2) {
        return this.second2time(Math.round((time2 - time1) / 1000));
    },
    second2time(sec) {

        let sec_num = Math.floor(sec);
        let hours = Math.floor(sec_num / 3600);
        let minutes = Math.floor((sec_num - (hours * 3600)) / 60);
        let seconds = sec_num - (hours * 3600) - (minutes * 60);

        if (hours < 10) {
            hours = "0" + hours;
        } else {
            hours = "" + hours;
        }

        if (minutes < 10) {
            minutes = "0" + minutes;
        }
        if (seconds < 10) {
            seconds = "0" + seconds;
        }

        let result = "";

        if (hours === '00') {
            result = minutes + ':' + seconds;
        } else {
            result = hours + ':' + minutes + ':' + seconds;
        }

        return result;
    },

    split3word(text) {

        if (text === undefined || text === null) {
            return "";
        }

        text = "" + text.replace(/[* ]/g, "");

        if (text.length <= 3) {
            return text;
        }

        let ntext = "";

        let count = 0;
        for (let i = 0; i < text.length; i++) {
            if (i !== 0 && i % 3 === 0 && count < 2) {
                ntext += " " + text.charAt(i);
                count++
            } else {
                ntext += text.charAt(i);
            }
        }

        return ntext.trim();
    },

    requestFullscreen(element, callback) {
        try {
            if (document.fullscreenEnabled) {
                if (element.requestFullscreen) {

                    element.requestFullscreen().then(() => {
                        if (callback) {
                            callback();
                        }
                    });

                } else if (element.mozRequestFullScreen) {

                    element.mozRequestFullScreen();
                    if (callback) {
                        callback();
                    }

                } else if (element.webkitRequestFullScreen) {

                    element.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
                    if (callback) {
                        callback();
                    }
                }
            }
        } catch (e) {
            console.debug(e);
        }
    },

    exitFullscreen() {
        try {
            if (document.exitFullscreen) {
                document.exitFullscreen()
                    .then(() => {
                        //console.debug(d);
                    }).catch(() => {
                        //console.debug(e);
                    });
            } else if (document.msExitFullscreen) {
                document.msExitFullscreen();
            }
        } catch (e) {
            console.debug(e);
        }
    },


