import langEN from "@/scripts/lang/starise-lang-en.js";
import langJA from "@/scripts/lang/starise-lang-ja.js";
import langCN from "@/scripts/lang/starise-lang-cn.js";
import langTW from "@/scripts/lang/starise-lang-tw.js";

import enLocal from 'date-fns/locale/en-US'
import twLocal from 'date-fns/locale/zh-TW'
import cnLocal from 'date-fns/locale/zh-CN'
import jaLocal from 'date-fns/locale/ja'


import _langKey from "@/scripts/lang/starise-lang-key.js";

export const langDefinition = {
    en: { font: "font-default", key: "en", value: "English", localeCode: "US", langCode: "enUS", local: enLocal },
    ja: { font: "font-ja", key: "ja", value: "日本語", localeCode: "JP", langCode: "jaJP", local: jaLocal },
    cn: { font: "font-cn", key: "cn", value: "簡体", localeCode: "CN", langCode: "zhCN", local: cnLocal },
    tw: { font: "font-tw", key: "tw", value: "繁體", localeCode: "TW", langCode: "zhTW", local: twLocal },
};

let defaultKey = (window.navigator.userLanguage || window.navigator.language).toLowerCase();
if (defaultKey === 'zh-tw') {
    defaultKey = 'tw';
} else if (defaultKey === 'zh-cn') {
    defaultKey = 'cn';
} else if (defaultKey === 'ja') {
    defaultKey = 'ja';
} else {
    defaultKey = 'en';
}

export const langKey = _langKey;

export const langControl = {
    initType: langDefinition[defaultKey].key,
    initName: langDefinition[defaultKey].value,
    localeCode: langDefinition[defaultKey].localeCode,
    langCode: langDefinition[defaultKey].langCode,
    local: langDefinition[defaultKey].local,
    types: [{
            name: langDefinition.en.value,
            value: langDefinition.en.key
        },
        {
            name: langDefinition.ja.value,
            value: langDefinition.ja.key
        },
        {
            name: langDefinition.cn.value,
            value: langDefinition.cn.key,
        },
        {
            name: langDefinition.tw.value,
            value: langDefinition.tw.key,
        },
    ],
    data: {
        en: langEN,
        tw: langTW,
        cn: langCN,
        ja: langJA,
    },
    get: function(_key, _default) {
        let result = this.data[this.initType][_key];
        if (result === undefined && _default !== undefined) {
            return _default;
        }
        if (result === undefined) {
            return _key;
        }
        return result;
    },
    change: function(_type) {
        if (_type === undefined || _type === null) {
            _type = langControl.initType;
        } else {
            langControl.initType = _type;
            langControl.initName = langDefinition[_type].value;
            langControl.localeCode = langDefinition[_type].localeCode;
            langControl.langCode = langDefinition[_type].langCode;
            langControl.local = langDefinition[_type].local;
        }

        for (let _key in langKey) {
            langKey[_key] = langControl.data[langControl.initType][_key];
        }

        localStorage.setItem("lang", langControl.initType);
    },
    getType: function() {
        return langControl.initType;
    },
    identify: function() {
        let original = (navigator.language + "").toLowerCase();
        switch (original) {

            case "ja":
                langControl.change(langDefinition.ja.key);
                break;
            case "zh-cn":
                langControl.change(langDefinition.cn.key);
                break;
            case "zh-sg":
                langControl.change(langDefinition.cn.key);
                break;
            case "zh-hk":
                langControl.change(langDefinition.tw.key);
                break;
            case "zh-tw":
                langControl.change(langDefinition.tw.key);
                break;
            default:
                langControl.change(langDefinition.en.key);
        }
    },
};

// 會直接將上次使用的語言裝載至 langKey
let _lang = localStorage.getItem("lang");
langControl.change(_lang);

window.langControl = langControl;