      <br />
      <br />
      <div class="randomize-choice">
        <span>Randomize Value: </span>
        <label
          ><input type="radio" v-model="showRandomize" v-bind:value="false" />False</label
        >
        <label
          ><input type="radio" v-model="showRandomize" v-bind:value="true" />True</label
        >
      </div>
      <br />
      <br />
      <div class="fixed-input" v-if="!showRandomize">
        <span>Fixed heartbeat: </span>
        <input input type="number" v-model="heartbeat" placeholder="Hearbeat in BPM" />
        <span> BPM </span>
        <br />
        <br />
        <span>Fixed SPO2: </span>
        <input input type="number" v-model="spo2" placeholder="SPO2 in %" />
        <span> % </span>
        <br />
        <br />
        <span>Fixed Temperature: </span>
        <input
          input
          type="number"
          v-model="temperature"
          placeholder="Temperature in celcius"
        />
        <span> &#8451; </span>
        <br />
        <br />
        <span>Fixed Blood Pressure: </span>
        <input
          input
          type="number"
          v-model="bloodPress"
          placeholder="Blood Pressure in mmHg"
        />
        <span> mmHg </span>
        <br />
        <br />
        <span>TOF Value: </span>
        <label
          ><input type="radio" v-model="tof" v-bind:value="'701'" />701 Leave 離床
        </label>
        <label
          ><input type="radio" v-model="tof" v-bind:value="'704'" />704 Fall 跌倒
        </label>
        <label
          ><input type="radio" v-model="tof" v-bind:value="'702'" />702 On the bed 在床上
        </label>
        <br /><br />
      </div>
      <div class="fixed-input" v-else>
        <span>Heartbeat Range: </span>
        <input
          input
          type="number"
          v-model="heartbeatMin"
          placeholder="Hearbeat Min Range"
        />
        <span> BPM </span>
        <span> - </span>
        <input
          input
          type="number"
          v-model="heartbeatMax"
          placeholder="Hearbeat Max Range"
        />
        <span> BPM </span>
        <br />
        <br />
        <span>SPO2 Range: </span>
        <input input type="number" v-model="SPO2Min" placeholder="SPO2 Min Range" />
        <span> % </span>
        <span> - </span>
        <input input type="number" v-model="SPO2Max" placeholder="SPO2 Max Range" />
        <span> % </span>
        <br />
        <br />
        <span>Temperature Range: </span>
        <input
          input
          type="number"
          v-model="tempMin"
          placeholder="Temperature Min Range"
        />
        <span> C </span>
        <span> - </span>
        <input
          input
          type="number"
          v-model="tempMax"
          placeholder="Temperature Max Range"
        />
        <span> C </span>
        <br />
        <br />
        <span>Blood Pressure Range: </span>
        <input
          input
          type="number"
          v-model="pressMin"
          placeholder="Blood pressure Min Range"
        />
        <span> mmHg </span>
        <span> - </span>
        <input
          input
          type="number"
          v-model="pressMax"
          placeholder="Blood pressure Max Range"
        />
        <span> mmHg </span>
        <br />
        <br />
        <span>TOF Choice to randomize: </span>
        <div v-for="role in alltof" :key="role.id">
          <input type="checkbox" v-model="tofChoice" :value="role" />
          <label>{{ role.name }}-{{ role.note }}</label>
        </div>
        <br />
        <span>Interval: </span>
        <input
          input
          type="number"
