        </tr>
        <tr v-if="showRandomize">
          <td>Heartbeat Range</td>
          <td>{{ heartbeatMin }} - {{ heartbeatMax }} BPM</td>
        </tr>
        <tr v-if="showRandomize">
          <td>SPO2 Range</td>
