          <td>
            <strong>{{ spo2 }} %</strong>
          </td>
        </tr>
        <tr>
          <td><strong>Temperature</strong></td>
          <td>
            <strong>{{ temperature }} C</strong>
          </td>
        </tr>
        <tr>
          <td><strong>Blood Pressure</strong></td>
          <td>
            <strong>{{ bloodPress }} mmHg</strong>
          </td>
        </tr>
        <tr>
          <td><strong>TOF</strong></td>
          <td>
            <strong>{{ tof }}</strong>
          </td>
        </tr>
      </table>

      <button
        v-if="randomizeStarted"
        class="submitButton"
        type="submit"
        @click="stopRandomize"
      >
        Stop Randomize
      </button>
      <button v-else class="submitButton" type="submit" @click="submit">Submit</button>
      <button class="submitButton" type="submit" @click="logout">Logout</button>
    </div>
  </div>
</template>

<script>
//Randomize bpm and spo2 import

import SockJS from "sockjs-client";
// import StompSupport from "@/scripts/StompSupport";
import Logger from "@/scripts/starise-logger";
import Stomp from "stompjs";

const logger = Logger("USER");

export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      // Data for generate random value
      isClickLogin: false,
      isLockMask: false,
      loadingText: null,
      isLoginError: false,
      alertMessage: null,
      userAccount: null,
      userPassword: null,

      webservice: {
        protocol: "https:",
        hostname: "starv-new.starise.network",
        port: "8443",
        contextPath: "/octon-aiot-webservice",
      },

      tofChoice: [
        {
          id: 1,
          name: "701",
          note: "Leave 離床",
        },
      ],

      alltof: [
        {
          id: 1,
          name: "701",
          note: "Leave 離床",
        },
        {
          id: 2,
          name: "704",
          note: "Fall 跌倒",
        },
        {
          id: 3,
          name: "702",
          note: "On the bed 在床上",
        },
      ],

      alreadyLogin: false,

      stompClient: null,

      username: "2007001140",
      hostname: "starv-new.starise.network",
      heartbeat: "",
      spo2: "",
      tof: "701",
      temperature: "36",
      bloodPress: "120",
      showRandomize: false,
      heartbeatMin: 40,
      heartbeatMax: 65,
      SPO2Min: 90,
      SPO2Max: 98,
      tempMin: 35,
      tempMax: 38,
      pressMin: 60,
      pressMax: 170,
      intervalRandomize: 1000,
      myInterval: null,
      stopInterval: false,
      randomizeStarted: false,
    };
  },
  created() {},
  mounted() {
    // Randomize the value of spo2 and heartbeat, and send to the server
  },
  methods: {
    login() {
      return new Promise((resolve) => {
        //webserviceHost = "https://renaih.starise.network:8443";

        const url = new URL(this.webservice.protocol + "//" + this.webservice.hostname);
        url.port = this.webservice.port;
        url.pathname = this.webservice.contextPath + "/ws";
        console.log("URL is %s", url.toString());

        const socket = new SockJS(url.toString());
        // socket.onerror = function () {
        //   logger.log("Socket error");
        //   StompSupport._connect();
        //   reject();
        //   setTimeout(() => {
        //     StompSupport._connect();
        //   }, 10000);
        // };

        socket.onclose = function () {
          logger.log("Socket closed");
        };

        this.stompClient = Stomp.over(socket);
        this.stompClient.connect(
          {
            Authorization: this.loginId,
          },
          () => {
            this.alreadyLogin = true;
            resolve();
          }
        );
      });
    },

    logout() {
      this.$parent.loginPassed = false;
    },

    stopRandomize() {
      this.stopInterval = true;
    },

    randomizeSPO2HeartbeatTOF() {
      this.spo2 =
        Math.floor(Math.random() * (this.SPO2Max - this.SPO2Min + 1)) + this.SPO2Min;
      this.heartbeat =
        Math.floor(Math.random() * (this.heartbeatMax - this.heartbeatMin + 1)) +
        this.heartbeatMin;

      this.temperature =
        Math.floor(Math.random() * (this.tempMax - this.tempMin + 1)) + this.tempMin;

      this.bloodPress =
        Math.floor(Math.random() * (this.pressMax - this.pressMin + 1)) + this.pressMin;

      this.tof = this.tofChoice[Math.floor(Math.random() * this.tofChoice.length)].name;

      this.sent();
    },

    submit() {
      if (this.alreadyLogin) {
        if (!this.showRandomize) {
          this.stompClient.send(
            "/app/user/healthInfo",
            {
              userId: this.username,
            },
            JSON.stringify({
              userId: this.username,
              bpm: this.heartbeat,
              spo2: this.spo2,
              temp: this.temperature,
              bloodPress: this.bloodPress,
              tof: this.tof,
            })
          );
        } else {
          this.randomizeStarted = true;
          this.myInterval = setInterval(
            () => this.randomizeSPO2HeartbeatTOF(),
            this.intervalRandomize
          );
        }
      } else {
        if (!this.showRandomize) {
          this.login().then(() => {
            this.stompClient.send(
              "/app/user/healthInfo",
              {
                userId: this.username,
              },
              JSON.stringify({
                userId: this.username,
                bpm: this.heartbeat,
                spo2: this.spo2,
                temp: this.temperature,
                bloodPress: this.bloodPress,
                tof: this.tof,
              })
            );
          });
        } else {
          this.login().then(() => {
            this.randomizeStarted = true;
            this.myInterval = setInterval(
              () => this.randomizeSPO2HeartbeatTOF(),
              this.intervalRandomize
            );
          });
        }
      }

      //this.stompClient.send("/app/system", {}, "123456");
    },
    sent() {
      if (this.username) {
        this.stompClient.send(
          "/app/user/healthInfo",
          {
            userId: this.username,
          },
          JSON.stringify({
            userId: this.username,
            bpm: this.heartbeat,
            spo2: this.spo2,
            temp: this.temperature,
            bloodPress: this.bloodPress,
            tof: this.tof,
          })
        );
        //this.stompClient.send("/app/system", {}, "123456");
      }
    },
  },
  watch: {
    stopInterval: function () {
      if (this.stopInterval) {
        if (this.myInterval) {
          clearInterval(this.myInterval);
          this.stopInterval = false;
          this.randomizeStarted = false;
        }
      }
    },
  },
};
</script>

