<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <!-- <HelloWorld msg="Welcome to Octon API Tester" /> -->
  <h1>Welcome to Octon API Tester</h1>

  <div v-if="!loginPassed" class="login">
    <span>Please login with one of the Starmed-ER account </span>
    <br />
    <br />
    <span>ID: </span>
    <input v-model="userAccount" />
    <br />
    <br />
    <span>Password: </span>
    <input type="password" v-model="userPassword" />
    <br />
    <br />
    <div class="randomize-choice">
      <span>Which API Tester: </span>
      <label><input type="radio" v-model="whichAPI" v-bind:value="'octon'" />Octon</label>
      <label
        ><input type="radio" v-model="whichAPI" v-bind:value="'imedtac'" />Imedtac</label
      >
    </div>
    <br />
    <button class="submitButton" type="submit" @click="onLogin" @keydown.enter="onLogin">
      Login
    </button>
  </div>
  <HelloImedtac
    v-if="loginPassed && whichAPI == 'imedtac'"
    msg="Welcome to Imedtac API Tester"
  />
  <HelloWorld
    v-if="loginPassed && whichAPI == 'octon'"
    msg="Welcome to Imedtac API Tester"
  />
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import HelloImedtac from "./components/HelloImedtac.vue";
import { langKey } from "@/scripts/starise-lang.js";
import axios from "axios";
import Logger from "@/scripts/starise-logger";

import webservice from "@/scripts/webservice";
import sha512 from "crypto-js/sha512";

const logger = Logger("USER");

export default {
  name: "App",
  components: {
    HelloImedtac,
    HelloWorld,
  },
  data() {
    return {
      loginPassed: false,
      whichAPI: "octon",
    };
  },
  methods: {
    onLogin() {
      if (this.isClickLogin) {
        return;
      }

      this.isClickLogin = true;

      logger.debug("onLogin");
      //this.$store.commit("setLoginTextStep");
      this.alertMessage = null;
      if (!this.userAccount || !this.userPassword) {
        this.alertMessage = langKey.INCORRECT_USERNAME_OR_PASSWORD;
      }

      if (this.alertMessage) {
        logger.debug("onLogin, alertMessage:%s", this.alertMessage);
        return;
      }

      this.directionLogin(this.userAccount, this.userPassword);
    },

    directionLogin(uid, pwd) {
      uid = uid + "";
      uid = uid.replace(/ /g, "");

      while (uid.startsWith("*") === true) {
        uid = uid.substring(1);
      }

      this.isLockMask = true;
      this.loadingText = "登入中...";

      let data = new FormData();
      data.set("uid", uid);
      data.set("pwd", sha512(pwd).toString());

      const instance = axios.create();
      instance.defaults.timeout = 20000;

      instance
        .post(webservice.loginUrl(), data)
        .then((result) => {
          if (result.data) {
            console.log("THIS IS RESULT DATA", result.data);
            if (result.data.code === "0000") {
              // 登入後比對帳號角色，不同，不行登入 master/user/any
              this.loginPassed = true;
            } else {
              this.loginPassed = false;

              this.isLockMask = false;
              alert("Incorrect Password");
            }
          }
        })
        .catch((e) => {
          logger.error(e);

          this.isLoginError = true;
          this.loadingText = e;
        })
        .finally(() => {
          this.isClickLogin = false;
        });
    },
  },
};
</script>

