<template>
  <div class="hello">
    <div class="input">
      <span>Hostname: </span>
      <input v-model="hostname" placeholder="Hostname" />
      <br />
      <br />
      <span>Account head number: </span>
      <input v-model="userHeadNumber" placeholder="Example : 200600 / 200500" />
      <br />
      <br />
      <span>Bed Number: </span>
      <input v-model="bedNumber" placeholder="Example: P110301" />
      <br />
      <br />
      <div class="randomize-choice">
        <span>Randomize Value: </span>
        <label
          ><input type="radio" v-model="showRandomize" v-bind:value="false" />False</label
        >
        <label
          ><input type="radio" v-model="showRandomize" v-bind:value="true" />True</label
        >
      </div>
      <br />
      <br />

      <!-- Table input Randomize-->
      <table v-if="showRandomize">
        <tr>
          <th>Indicator</th>
          <th>Input</th>
          <th>Current Value</th>
        </tr>
        <tr>
          <td>Hostname</td>
          <td>{{ webservice.protocol }}// {{ hostname }}</td>
          <td>
            <strong>{{ webservice.protocol }}//{{ hostname }}</strong>
          </td>
        </tr>

        <!-- Bed Number -->
        <tr>
          <td>Bed Number</td>
          <td>{{ bedNumber }}</td>
          <td>
            <strong>{{ bedNumber }}</strong>
          </td>
        </tr>

        <!-- Heartbeat NBP Range -->
        <tr>
          <td>Heartbeat NBP Range</td>
          <td>
            <input
              input
              type="number"
              v-model="heartbeatNBPMin"
              placeholder="Hearbeat Min Range"
            />
            -
            <input
              input
              type="number"
              v-model="heartbeatNBPMax"
              placeholder="Hearbeat Max Range"
            />
            BPM
          </td>
          <td>
            <strong>{{ heartbeatNBP }} BPM</strong>
          </td>
        </tr>

        <!-- Heartbeat Range -->
        <tr>
          <td>Heartbeat SPO2 Range</td>
          <td>
            <input
              input
              type="number"
              v-model="heartbeatSPOMin"
              placeholder="Hearbeat Min Range"
            />
            -
            <input
              input
              type="number"
              v-model="heartbeatSPOMax"
              placeholder="Hearbeat Max Range"
            />
            BPM
          </td>
          <td>
            <strong>{{ heartbeatSPO2 }} BPM</strong>
          </td>
        </tr>

        <!-- SPO2 Range -->
        <tr>
          <td>SPO2 Range</td>
          <td>
            <input input type="number" v-model="SPO2Min" placeholder="SPO2 Min Range" /> -
            <input input type="number" v-model="SPO2Max" placeholder="SPO2 Max Range" />
            %
          </td>
          <td>
            <strong>{{ spo2 }} %</strong>
          </td>
        </tr>

        <!-- Respiratory range -->
        <tr>
          <td>Respiratory Range</td>
          <td>
            <input input type="number" v-model="respMin" placeholder="Resp Min Range" /> -
            <input input type="number" v-model="respMax" placeholder="Resp Max Range" />
            mmHg
          </td>
          <td>
            <strong>{{ resp }} %</strong>
          </td>
        </tr>

        <!-- Temperatur Range -->
        <tr>
          <td>Temperature Range</td>
          <td>
            <input
              input
              type="number"
              v-model="tempMin"
              placeholder="Temperature Min Range"
            />
            -
            <input
              input
              type="number"
              v-model="tempMax"
              placeholder="Temperature Max Range"
            />
            C
          </td>
          <td>
            <strong>{{ temperature }} C</strong>
          </td>
        </tr>

        <!-- Glucose Range -->
        <tr>
          <td>Glucose Range</td>
          <td>
            <input
              input
              type="number"
              v-model="glucoseMin"
              placeholder="Glucose Min Range"
            />
            -
            <input
              input
              type="number"
              v-model="glucoseMax"
              placeholder="Glucose Max Range"
            />
            mg/dL
          </td>
          <td>
            <strong>{{ glucose }} mg/dL</strong>
          </td>
        </tr>

        <!-- NBP Sys Range -->
        <tr>
          <td>NBP Sys Range</td>
          <td>
            <input
              input
              type="number"
              v-model="nbpSysMin"
              placeholder="NBP Sys Min Range"
            />
            -
            <input
              input
              type="number"
              v-model="nbpSysMax"
              placeholder="NBP Sys Max Range"
            />
            mmHg
          </td>
          <td>
            <strong>{{ nbpSys }} mmHg</strong>
          </td>
        </tr>

        <!-- NBP Dias Range -->
        <tr>
          <td>NBP Dias Range</td>
          <td>
            <input
              input
              type="number"
              v-model="nbpDiasMin"
              placeholder="NBP Dias Min Range"
            />-
            <input
              input
              type="number"
              v-model="nbpDiasMax"
              placeholder="NBP Dias Max Range"
            />
            mmHg
          </td>
          <td>
            <strong>{{ nbpDias }} mmHg</strong>
          </td>
        </tr>

        <!-- Interval -->
        <tr>
          <td>Interval</td>
          <td>
            <input
              input
              type="number"
              v-model="intervalRandomize"
              placeholder="Interval in ms"
            />
            ms
          </td>
          <td>
            <strong>{{ intervalRandomize }} ms</strong>
          </td>
        </tr>
      </table>

      <!-- Table not randomize -->
      <table v-else>
        <tr>
          <th>Indicator</th>
          <th>Input</th>
        </tr>
        <tr>
          <td>Hostname</td>
          <td>{{ webservice.protocol }}//{{ hostname }}s</td>
        </tr>
        <tr>
          <td>Bed Number</td>
          <td>{{ bedNumber }}</td>
        </tr>
        <tr>
          <td><strong>HR NBP</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="heartbeatNBP"
                placeholder="HB NBP in BPM"
              />
              BPM</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>HR SPO2</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="heartbeatSPO2"
                placeholder="HB SPO2 in BPM"
              />
              BPM</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>SPO2</strong></td>
          <td>
            <strong>
              <input input type="number" v-model="spo2" placeholder="SPO2 in %" />
              %</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>Resp</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="resp"
                placeholder="Respiratory in mmHg"
              />
              %</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>Temperature</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="temperature"
                placeholder="Temperature in celcius"
              />
              C</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>Glucose</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="glucose"
                placeholder="Glucose in mg/dL"
              />
              mg/dL</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>NBP Sys</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="nbpSys"
                placeholder="Blood Pressure Systole mmHg"
              />
              mmHg</strong
            >
          </td>
        </tr>
        <tr>
          <td><strong>NBP Dias</strong></td>
          <td>
            <strong>
              <input
                input
                type="number"
                v-model="nbpDias"
                placeholder="Blood Pressure Diastole mmHg"
              />
              mmHg</strong
            >
          </td>
        </tr>
      </table>

      <button
        v-if="randomizeStarted"
        class="submitButton"
        type="submit"
        @click="stopRandomize"
      >
        Stop Randomize
      </button>
      <button v-else class="submitButton" type="submit" @click="submit">Submit</button>
      <button class="submitButton" type="submit" @click="logout">Logout</button>
    </div>
  </div>
</template>

<script>
//Randomize bpm and spo2 import

import SockJS from "sockjs-client";
// import StompSupport from "@/scripts/StompSupport";
import Logger from "@/scripts/starise-logger";
import Stomp from "stompjs";
import { langKey } from "@/scripts/starise-lang.js";
import axios from "axios";
import webservice from "@/scripts/webservice";
import sha512 from "crypto-js/sha512";

const logger = Logger("USER");

export default {
  name: "HelloImedtac",
  props: {
    msg: String,
  },
  data() {
    return {
      // Data for generate random value
      isClickLogin: false,
      isLockMask: false,
      loadingText: null,
      isLoginError: false,
      alertMessage: null,
      loginPassed: false,
      userAccount: null,
      userPassword: null,
      userHeadNumber: "200600",

      webservice: {
        protocol: "https:",
        hostname: "starv-new.starise.network",
        port: "8443",
        contextPath: "/octon-aiot-webservice",
      },

      alreadyLogin: false,

      stompClient: null,

      bedNumber: "P1110301",
      hostname: "starv-new.starise.network",
      heartbeatNBP: "95",
      heartbeatSPO2: "98",
      spo2: "97",
      resp: "200",
      temperature: "36.8",
      glucose: "80",
      nbpDias: "90",
      nbpSys: "120",

      showRandomize: false,
      heartbeatNBPMin: 40,
      heartbeatNBPMax: 65,
      heartbeatSPOMin: 45,
      heartbeatSPOMax: 90,
      respMin: 90,
      respMax: 100,
      glucoseMin: 100,
      glucoseMax: 400,
      nbpSysMin: 80,
      nbpSysMax: 120,
      SPO2Min: 90,
      SPO2Max: 98,
      tempMin: 35,
      tempMax: 38,
      nbpDiasMin: 60,
      nbpDiasMax: 170,
      intervalRandomize: 1000,
      myInterval: null,
      stopInterval: false,
      randomizeStarted: false,
    };
  },
  created() {},
  mounted() {
    // Randomize the value of spo2 and heartbeatNBP, and send to the server
  },
  methods: {
    loginVital() {
      return new Promise((resolve) => {
        const url = new URL(
          "https://starv-new.starise.network:8443/octon-aiot-webservice/"
        );
        url.port = "8443";
        url.pathname = "/octon-aiot-webservice/ws";

        const socket = new SockJS(url.toString());
        socket.onclose = function () {
          logger.log("Socket closed");
        };

        this.stompClient = Stomp.over(socket);
        this.stompClient.connect(
          {
            Authorization: this.userAccount,
          },
          () => {
            this.alreadyLogin = true;
            resolve(this.alreadyLogin);
          }
        );
      });
    },

    directionLogin(uid, pwd) {
      uid = uid + "";
      uid = uid.replace(/ /g, "");

      while (uid.startsWith("*") === true) {
        uid = uid.substring(1);
      }

      this.isLockMask = true;
      this.loadingText = "登入中...";

      let data = new FormData();
      data.set("uid", uid);
      data.set("pwd", sha512(pwd).toString());

      const instance = axios.create();
      instance.defaults.timeout = 20000;

      instance
        .post(webservice.loginUrl(), data)
        .then((result) => {
          if (result.data) {
            console.log("THIS IS RESULT DATA", result.data);
            if (result.data.code === "0000") {
              // 登入後比對帳號角色，不同，不行登入 master/user/any
              this.loginPassed = true;
            } else {
              this.loginPassed = false;

              this.isLockMask = false;
              alert("Incorrect Password");
            }
          }
        })
        .catch((e) => {
          logger.error(e);

          this.isLoginError = true;
          this.loadingText = e;
        })
        .finally(() => {
          this.isClickLogin = false;
        });
    },

    logout() {
      this.$parent.loginPassed = false;
    },

    stopRandomize() {
      this.stopInterval = true;
    },

    SendVital() {
      // If not yet login , then try to login to websocket
      if (!this.alreadyLogin) {
        this.loginVital().then(() => {
          // Send here after login
          this.stompClient.send(
            "/app/user/healthInfo",
            {
              userId: this.userHeadNumber,
            },
            JSON.stringify({
              CHART_NO: this.bedNumber,
              HR: {
                Unit: "BPM",
                NBPValue: this.heartbeatNBP,
                SPO2Value: this.heartbeatSPO2,
              },
              SPO2: {
                Unit: "%",
                Value: this.spo2,
              },
              Resp: {
                Unit: "mmHg",
                Value: this.resp,
              },
              Temp: {
                Unit: "Celcius",
                Value: this.temperature,
              },
              Glucose: {
                Unit: "mg/dL",
                Value: this.glucose,
              },
              NBP: {
                Unit: "mmHg",
                SYS_Value: this.nbpSys,
                DIA_Value: this.nbpDias,
              },
            })
          );
        });
      } else {
        //Send directly if already login, no need to login again
        this.stompClient.send(
          "/app/user/healthInfo",
          {
            userId: this.userHeadNumber,
          },
          JSON.stringify({
            CHART_NO: this.bedNumber,
            HR: {
              Unit: "BPM",
              NBPValue: this.heartbeatNBP,
              SPO2Value: this.heartbeatSPO2,
            },
            SPO2: {
              Unit: "%",
              Value: this.spo2,
            },
            Resp: {
              Unit: "mmHg",
              Value: this.resp,
            },
            Temp: {
              Unit: "Celcius",
              Value: this.temperature,
            },
            Glucose: {
              Unit: "mg/dL",
              Value: this.glucose,
            },
            NBP: {
              Unit: "mmHg",
              SYS_Value: this.nbpSys,
              DIA_Value: this.nbpDias,
            },
          })
        );
      }
    },

    randomizeEverything() {
      this.heartbeatNBP =
        Math.floor(Math.random() * (this.heartbeatNBPMax - this.heartbeatNBPMin + 1)) +
        this.heartbeatNBPMin;
      this.heartbeatSPO2 =
        Math.floor(Math.random() * (this.heartbeatSPOMax - this.heartbeatSPOMin + 1)) +
        this.heartbeatSPOMin;
      this.spo2 =
        Math.floor(Math.random() * (this.SPO2Max - this.SPO2Min + 1)) + this.SPO2Min;
      this.resp =
        Math.floor(Math.random() * (this.respMax - this.respMin + 1)) + this.respMin;
      this.temperature =
        Math.floor(Math.random() * (this.tempMax - this.tempMin + 1)) + this.tempMin;
      this.glucose =
        Math.floor(Math.random() * (this.glucoseMax - this.glucoseMin + 1)) +
        this.glucoseMin;
      this.nbpSys =
        Math.floor(Math.random() * (this.nbpSysMax - this.nbpSysMin + 1)) +
        this.nbpDiasMin;
      this.nbpDias =
        Math.floor(Math.random() * (this.nbpDiasMax - this.nbpDiasMin + 1)) +
        this.nbpDiasMin;
      this.SendVital();
    },

    onLogin() {
      if (this.isClickLogin) {
        return;
      }

      this.isClickLogin = true;

      logger.debug("onLogin");
      //this.$store.commit("setLoginTextStep");
      this.alertMessage = null;
      if (!this.userAccount || !this.userPassword) {
        this.alertMessage = langKey.INCORRECT_USERNAME_OR_PASSWORD;
      }

      if (this.alertMessage) {
        logger.debug("onLogin, alertMessage:%s", this.alertMessage);
        return;
      }

      this.directionLogin(this.userAccount, this.userPassword);
    },

    submit() {
      if (this.alreadyLogin) {
        if (!this.showRandomize) {
          this.stompClient.send(
            "/app/user/healthInfo",
            {
              userId: this.userHeadNumber,
            },
            JSON.stringify({
              CHART_NO: this.bedNumber,
              HR: {
                Unit: "BPM",
                NBPValue: this.heartbeatNBP,
                SPO2Value: this.heartbeatSPO2,
              },
              SPO2: {
                Unit: "%",
                Value: this.spo2,
              },
              Resp: {
                Unit: "mmHg",
                Value: this.resp,
              },
              Temp: {
                Unit: "Celcius",
                Value: this.temperature,
              },
              Glucose: {
                Unit: "mg/dL",
                Value: this.glucose,
              },
              NBP: {
                Unit: "mmHg",
                SYS_Value: this.nbpSys,
                DIA_Value: this.nbpDias,
              },
            })
          );
        } else {
          this.randomizeStarted = true;
          this.myInterval = setInterval(
            () => this.randomizeEverything(),
            this.intervalRandomize
          );
        }
      } else {
        if (!this.showRandomize) {
          this.loginVital().then(() => {
            this.stompClient.send(
              "/app/user/healthInfo",
              {
                userId: this.userHeadNumber,
              },
              JSON.stringify({
                CHART_NO: this.bedNumber,
                HR: {
                  Unit: "BPM",
                  NBPValue: this.heartbeatNBP,
                  SPO2Value: this.heartbeatSPO2,
                },
                SPO2: {
                  Unit: "%",
                  Value: this.spo2,
                },
                Resp: {
                  Unit: "mmHg",
                  Value: this.resp,
                },
                Temp: {
                  Unit: "Celcius",
                  Value: this.temperature,
                },
                Glucose: {
                  Unit: "mg/dL",
                  Value: this.glucose,
                },
                NBP: {
                  Unit: "mmHg",
                  SYS_Value: this.nbpSys,
                  DIA_Value: this.nbpDias,
                },
              })
            );
          });
        } else {
          this.loginVital().then(() => {
            this.randomizeStarted = true;
            this.myInterval = setInterval(
              () => this.randomizeEverything(),
              this.intervalRandomize
            );
          });
        }
      }

      //this.stompClient.send("/app/system", {}, "123456");
    },
  },
  watch: {
    stopInterval: function () {
      if (this.stopInterval) {
        if (this.myInterval) {
          clearInterval(this.myInterval);
          this.stopInterval = false;
          this.randomizeStarted = false;
        }
      }
    },
  },
};
</script>

