import { SETTING } from "@/scripts/starise-page";


export default {

    baseUrl() {

        let url;

        if (SETTING.localDomain.enabled && SETTING.localDomain.hostname) {
            url = new URL(SETTING.localDomain.protocol + "//" + SETTING.localDomain.hostname);
            url.port = SETTING.localDomain.port;
        } else {
            url = new URL(SETTING.webservice.protocol + "//" + SETTING.webservice.hostname);
            url.port = SETTING.webservice.port;
        }

        return url;
    },

    monitorListUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/monitorsetting/list";
        return url.toString();
    },

    monitorSaveUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/monitorsetting/save";
        return url.toString();
    },

    monitorDeleteUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/monitorsetting/delete";
        return url.toString();
    },

    locationListUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/location/list";
        return url.toString();
    },

    locationSaveUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/location/save";
        return url.toString();
    },

    locationDeleteUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/location/delete";
        return url.toString();
    },

    loginUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/login";
        return url.toString();
    },

    dataUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/data";
        return url.toString();
    },

    sockjsUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/ws";
        return url.toString();
    },

    settingGetUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/setting/get";
        return url.toString();
    },

    autoUpdateUrl() {
        let url = this.baseUrl();
        url.pathname = SETTING.webservice.contextPath + "/autoUpdate";
        return url.toString();
    }

}