export const CLIENT = {

    //direction: null,
    init: null,
    language: langControl.getType(),
    isEncrypted: false,
    user: null,
    pass: null,
    pass512: null,
    webSocketURL: null,
    webrtcURL: null,

    storeClientKey: "*desktop_client",

    clear() {
        window.sessionStorage.removeItem(this.storeClientKey);
    },
    store() {

        window.sessionStorage.setItem(
            this.storeClientKey,
            UTIL.encode(JSON.stringify(this))
        );
    },
    restore() {

        let item = window.sessionStorage.getItem(this.storeClientKey);
        if (item) {
            try {
                item = JSON.parse(UTIL.decode(item));

                for (let key of Object.keys(item)) {
                    this[key] = item[key];
                }

                if (item.init && item.user && item.pass && item.language) {
                    return true;
                }
            } catch (e) {
                //
            }
        }

        return false;
    },
};


export const PAGE = {

    notifySecond: 2,

    notifyError: function(_message) {
        let _msg = _message === undefined ? "System Error" : _message;

        $.wnoty({
            type: "error",
            message: _msg,
            autohideDelay: this.notifySecond * 1000,
        });

        console.debug("#[PAGE] " + _msg);
    },

    notifyMessage: function(_message) {
        let _msg = _message === undefined ? "System Error" : _message;

        $.wnoty({
            type: "info",
            message: _msg,
            autohideDelay: this.notifySecond * 1000,
        });

        console.debug("#[PAGE] " + _msg);
    },

    clearTimeout: function() {},

    alert: function(msg) {
        //alert(msg);
        PAGE.notifyError(msg);
    },
};

/**
 * 系統載入時使用的快取資料
 */
export const LocalData = {

    key: {

        version: "AIOT_VERSION",

        userId: "AIOT_A",

        userIDHistory: "AIOT_userIDHistory",

        starvRecordingData: "AIOT_starvRecordingData",

        NotificationPermission: "AIOT_NotificationPermission",

        ptzPort: "AIOT_PTZ_PORT",

        localDomain: "AIOT_localDomain",

        tobii: "AOIT_tobii",

        devices: "AOIT_devices",
    },

    expiryTime: 60000 * 60 * 8,

    unreadCountData: {
        userId: null,
        data: {}
    },


    setItem(key, data) {

        //console.log("setItem [%s] = [%s]，(debug=%s)", key, data, SETTING.debug);

        try {
            //if (SETTING.debug) {
            //    window.localStorage.setItem(key, data);
            //} else {
            window.localStorage.setItem(key, UTIL.encode(data));
            //}
        } catch (e) {
            console.error(e)
        }

    },
    getItem(key, defaultValue) {
        try {

            let temp = window.localStorage.getItem(key);
            if (temp) {
                //if (SETTING.debug) {
                //    return temp;
                //} else {
                return UTIL.decode(temp);
                //}
            }
        } catch (e) {
            console.error(e)
        }

        if (defaultValue) {
            return defaultValue;
        }
    },

    removeItem(key) {
        try {
            return window.localStorage.removeItem(key);
        } catch (e) {
            console.error(e)
        }
    },


    storeMap(key, dataMap) {

        //console.debug("#[LocalData] STORE " + key + " size:" + dataMap.size);
        this.setItem(key, JSON.stringify({
            userId: CLIENT.init.userID,
            data: Array.from(dataMap.entries()),
            time: new Date().getTime(),
        }));
    },

    fetchMap(key) {

        console.debug("#[LocalData] LOAD:" + key)
        let temp = this.getItem(key);
        if (temp) {
            let { userId, data } = JSON.parse(temp);
            if (userId === CLIENT.init.userID) {
                return new Map(data);
            }
        }
    },

    storeList(key, dataList) {
        //console.debug("#[LocalData] STORE " + key + " size:" + dataList.length);
        this.setItem(key, JSON.stringify({
            userId: CLIENT.init.userID,
            data: dataList,
            time: new Date().getTime()
        }));
    },

    fetchList(key) {

        let temp = this.getItem(key);
        if (temp) {
            let { userId, data } = JSON.parse(temp);
            if (userId === CLIENT.init.userID) {
                return data;
            }
        }
    },


    storeUserId(userId) {
        this.setItem(this.key.userId, userId);
    },

    getUserId() {
        return this.getItem(this.key.userId);
    },

    recordUserID(userID) {

        let userIDHistory = this.getUserIDHistory();
        if (userIDHistory.length >= 10) {
            userIDHistory.pop();
        }

        let i = userIDHistory.length;
        while (i--) {
            if (userIDHistory[i] === userID) {
                userIDHistory.splice(i, 1);
            }
        }

        userIDHistory = [userID].concat(userIDHistory);

        this.setItem(this.key.userIDHistory, JSON.stringify(userIDHistory));
    },

    getUserIDHistory() {

        let _temp = this.getItem(this.key.userIDHistory);
        if (!_temp) {
            _temp = [];
        } else {
            _temp = JSON.parse(_temp);
        }

        return _temp;
    },


    storeNotificationPermission(permission) {
        if (permission) {
            this.setItem(this.key.NotificationPermission, permission);
        } else {
            this.removeItem(this.key.NotificationPermission);
        }
    },

    getNotificationPermission() {
        return this.getItem(this.key.NotificationPermission);
    },


    storeLocalDomain(localDomain) {
        if (localDomain) {
            this.setItem(this.key.localDomain, localDomain);
        } else {
            this.removeItem(this.key.localDomain);
        }
    },

    getLocalDomain() {
        return this.getItem(this.key.localDomain);
    },

    storePTZPort(ptzPort) {
        if (ptzPort) {
            this.setItem(this.key.ptzPort, ptzPort);
        } else {
            this.removeItem(this.key.ptzPort);
        }
    },

    getTobii() {
        let tobii = this.getItem(this.key.tobii);
        if (tobii) {
            return JSON.parse(tobii);
        } else {
            return {
                "tobiiTopOffset": 0,
                "tobiiLeftOffset": 0,
                "tobiiMaxWidth": 1920,
                "tobiiMaxHeight": 1080
            }
        }
    },

    storeTobii(tobii) {
        if (tobii) {
            this.setItem(this.key.tobii, JSON.stringify(tobii));
        } else {
            this.removeItem(this.key.tobii);
        }
    },
    getDevices() {

        let devices = this.getItem(this.key.devices);
        if (devices) {
            return JSON.parse(devices);
        }

        return null;
    },


    storeDevices(devices) {
        if (devices) {
            this.setItem(this.key.devices, JSON.stringify(devices));
        } else {
            this.removeItem(this.key.devices);
        }
    },

    getPTZPort() {
        return this.getItem(this.key.ptzPort);
    },

    /** 印出使用量 */
    calcLocalStorageSize() {
        let total = 0,
            size, key;
        for (key of Object.keys(localStorage)) {
            size = ((localStorage[key].length + key.length) * 2);
            total += size;
            console.log(key.substr(0, 50) + " = " + (size / 1024).toFixed(2) + " KB")
        }
        console.debug("Total = " + (total / 1024).toFixed(2) + " KB");
    },


    printAllItems() {

        let keies = Object.keys(this.key);
        keies = keies.sort();
        for (let k of keies) {
            let data = this.getItem(this.key[k]);
            if (data) {
                console.log("[" + k + "(" + UTIL.decode(this.key[k]) + ")] = " + data);
            }
        }
    },


    removeAll() {
        for (let k of Object.keys(this.key)) {
            window.localStorage.removeItem(this.key[k]);
        }
    },

    init() {

        console.debug("#[LocalData] init, debug:" + SETTING.debug)

        for (let k of Object.keys(this.key)) {
            this.key[k] = UTIL.encode(this.key[k]);
        }

        let version = this.getItem(this.key.version);

        // 版號不同重置所有暫存
        if (!version || version !== Version) {
            console.debug("#[LocalData] window.sessionStorage.clear");
            window.sessionStorage.clear();
        }

        this.setItem(this.key.version, Version);

        if (SETTING.debug) {
            window.sessionStorage.setItem("dev", "Y");
        }
    }
}

window.LOCALDATA = LocalData;
window.SETTING = SETTING;