import {SETTING} from "@/scripts/starise-page";
import {UTIL} from "@/scripts/starise-util.js";

let maxLogNameLength = 0;

export default function(name, style = 'color:#000000', isClone = true) {

    let logName = name;
    if (maxLogNameLength === 0 || logName.length > maxLogNameLength) {
        maxLogNameLength = logName.length;
    }

    switch (name) {
        case "IMWS": {
            style = "color:#FF7F00;background-color:#EEEEEE;";
            break;
        }
        case "SENT": {
            style = "color:blue;background-color:#EEEEEE;";
            break;
        }
        case "RECV": {
            style = "color:green;background-color:#EEEEEE;";
            break;
        }
    }

    return {

        nameStyle: style,
        prefix: "",

        setPrefix(prefix) {
            this.prefix = prefix;
        },

        setNameStyle(style) {
            this.nameStyle = style;
        },

        debug(log, ...values) {
            this._log(log, values, 'debug');
        },

        warn(log, ...values) {
            this._log(log, values, 'warn');
        },

        error(log, ...values) {
            this._log(log, values, 'error');
        },

        log(log, ...values) {
            this._log(log, values, 'debug');
        },

        _log(log, values = [], type = 'debug') {

            while (logName.length < maxLogNameLength) {
                logName = " " + logName;
            }


            let p1 = `%c#[${logName}]%c[%s]:%c, ${this.prefix}${log}`;


            let p2 = [this.nameStyle, "color:#CCCCCC", UTIL.getLogggerDateTime(), "color:black"].concat(values);
            //
            // if (!callback && SETTING.debug && false) {
            //     let callerName;
            //     // strict mode下無法取得FUNCTION.caller，故使用throw機制，
            //     // 僅在debug下生效
            //     try {
            //         throw new Error();
            //     } catch (e) {
            //         callerName = e.stack.split("at ")[2].replaceAll(/[\n ]+/g, "");
            //         callerName = callerName.substring(callerName.lastIndexOf("/"));
            //     }
            //     //console.log(callerName);
            //
            //     p1 += `%c - (${callerName})`;
            //     p2.push("color:#cccccc");
            // }
            //

            if (SETTING.debug) {

                if (isClone) {
                    for (let i = 0; i < p2.length; i++) {
                        if (p2[i] && typeof p2[i] === "object") {
                            let obj = UTIL.cloneObject(p2[i]);
                            if (SETTING.debugObjectToJson) {
                                p2[i] = JSON.stringify(obj, '', 2);
                            } else {
                                p2[i] = obj;
                            }
                        }
                    }
                }

                if (SETTING.debugObjectToJson) {
                    p1 = p1.replaceAll("%O", "%s");
                }

                if (type === 'debug') {
                    console.debug(p1, ...p2);
                } else if (type === 'warn') {
                    console.warn(p1, ...p2);
                } else {
                    console.error(p1, ...p2);
                }
            }

            if (window.electron && window.electron.ipcRenderer) {

                let re = /%[\w]/;
                let data;
                values = [].concat(values);
                while ((data = values.shift())) {
                    if (typeof data === "object") {
                        data = UTIL.cloneObject(data);
                    }

                    log = log.replace(re, data);
                }

                let logText = `#[${name}] ${log}`;

                if (values.length > 0) {
                    logText += " " + values.join(" ");
                }

                window.electron.ipcRenderer.send('stariseLogger', logText);
            }
        }
    }

}
